.simple {
    position: relative;
    width: 600px;
    margin: auto;
}

.wrapper {
    width: 480px;
    height: 600px;
    margin: auto;
}

.item {
    position: absolute;
    width: 480px;
    height: 100%;
    opacity: 0;
    transition: opacity 500ms linear;
}

.item:first-child {
    opacity: 1;
    z-index: 1;
}

img {
    width: 100%;
    height: 100%;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    object-fit: cover;
}

.control {
    position: absolute;
    top: calc(50% - 16px);
    width: 100%;
    height: auto;
}

.control-button_next,
.control-button_prev {
    position: absolute;
    top: 0;
    left: 0;
    padding: 2px 9px 2px 8px;
    border: 1px solid #000;
    border-radius: 50%;
    background: #fff ;
    cursor: pointer;
    color: #333;
    height: 25px;
    width: 25px;
    outline: none;
    z-index: 2;
}

.control-button_next {
    left: auto;
    right: 0;
    background: #fff ;
}

.restore {
    position: static;
    opacity: 1;
}